.profile {
  min-height: 100vh;
}

.profile__container {
  align-items: center;
}

.profile__title {
  margin: 74px 0 0 0;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #FFFFFF;
}

.profile__form {
  margin-top: 123px;
  width: 410px;
  display: flex;
  flex-direction: column;
}

.profile__label-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.profile__label {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  height: 14px;
}

.profile__border {
  margin: 4px 0 17px 0;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #424242;
}

.profile__text {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
  color: #FFFFFF;
}

.profile__input {
  padding: 0;
  background-color: #202020;
  border: none;
  outline: none;
  width: 80%;
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  text-align: right;
  color: #FFFFFF;
}

.profile__input-error {
  display: none;
}

.profile__input-error_active,
.profile__input-error {
  margin: 10px 0 0 0;
  display: flex;
  justify-content: flex-end;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #EE3465;
}

.profile__input:focus {
  border: none;
}

.profile__input::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  text-align: right;
  color: #FFFFFF;
}

.profile__submit-container {
  margin-top: 212px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.profile__form-button,
.profile__form-button_disabled {
  padding: 0;
  margin: 0;
  background-color: #202020;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #FFFFFF;
}

.profile__form-button_disabled {
  opacity: 0.3;
}

.profile__form-button:hover {
  opacity: 0.7;
  cursor: pointer;
}

.profile__error {
  display: none;
}

.profile__error_active,
.profile__error {
  margin: 0 0 20px 0;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #EE3465;
}

.profile__info {
  display: none;
}

.profile__info_active,
.profile__info {
  margin: 0 0 20px 0;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #3DDC84;
}

.profile__button-exit {
  padding: 0;
  margin: 16px 0 70px 0;
  border: none;
  background-color: #202020;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  color: #EE3465;
}

.profile__button-exit {
  opacity: 0.7;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .profile__title {
    margin: 236px 0 0 0;
  }

  .profile__form {
    margin-top: 96px;
  }

  .profile__form-button {
    margin: 210px 0 0 0;
  }

  .profile__button-exit{
    margin: 16px 0 269px 0;
  }
}

@media screen and (max-width: 426px) {
  .profile__title {
    margin: 70px 0 0 0;
  }

  .profile__form {
    margin-top: 80px;
    width: 260px;
  }

  .profile__form-button {
    margin: 379px 0 0 0;
    font-size: 12px;
    line-height: 15px;
  }

  .profile__button-exit{
    margin: 16px 0 40px 0;
    font-size: 12px;
    line-height: 15px;
  }
}
